import type React from "react"
import { useState } from "react"
// import { Input } from "@/components/ui/input"
// import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "../Elements/Card"
import { X } from "lucide-react"

interface TagProps {
  text: string
  onRemove: () => void
}

const Tag: React.FC<TagProps> = ({ text, onRemove }) => (
  <div className="bg-secondary text-secondary-foreground py-1 px-4 rounded-full flex items-center">
    <span className="text-sm">{text}</span>
    <button onClick={onRemove} className="ml-2 bg-primary text-primary-foreground px-2 rounded-full">
      <X size={12} />
    </button>
  </div>
)

interface TagInputProps {
  title: string
  placeholder: string
  tags: string[]
  onAddTag: (tag: string) => void
  onRemoveTag: (tag: string) => void
  showMandatoryButton?: boolean
}

const TagInput: React.FC<TagInputProps> = ({
  title,
  placeholder,
  tags,
  onAddTag,
  onRemoveTag,
  showMandatoryButton = false,
}) => {
  const [input, setInput] = useState("")

  const handleAddTag = () => {
    if (input.trim()) {
      onAddTag(input.trim())
      setInput("")
    }
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>{title}</CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="flex gap-2">
          <input
            type="text"
            placeholder={placeholder}
            value={input}
            onChange={(e) => setInput(e.target.value)}
            className="flex-grow"
          />
          <button onClick={handleAddTag}>
            Add
          </button>
          {showMandatoryButton && (
            <button onClick={() => onAddTag(`${input.trim()} (Mandatory)`)}>
              Add as Mandatory
            </button>
          )}
        </div>
        {tags.length > 0 && (
          <div>
            <p className="text-sm mb-2">Added {title}:</p>
            <div className="flex flex-wrap gap-2">
              {tags.map((tag, index) => (
                <Tag key={index} text={tag} onRemove={() => onRemoveTag(tag)} />
              ))}
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  )
}

interface SingleInputProps {
  title: string
  placeholder: string
  value: string
  onChange: (value: string) => void
}

const SingleInput: React.FC<SingleInputProps> = ({ title, placeholder, value, onChange }) => (
  <Card>
    <CardHeader>
      <CardTitle>{title}</CardTitle>
    </CardHeader>
    <CardContent>
      <input type="text" className="w-full h-8 rounded-sm" placeholder={placeholder} value={value} onChange={(e) => onChange(e.target.value)} />
    </CardContent>
  </Card>
)

export default function JobSearchForm() {
  const [technologies, setTechnologies] = useState<string[]>([])
  const [companies, setCompanies] = useState<string[]>([])
  const [domains, setDomains] = useState<string[]>([])
  const [preferredLocations, setPreferredLocations] = useState<string[]>([])
  const [techRole, setTechRole] = useState("")
  const [currentLocation, setCurrentLocation] = useState("")
  const [qualification, setQualification] = useState("")
  const [experience, setExperience] = useState("")
  const [salary, setSalary] = useState("")
  const [noticePeriod, setNoticePeriod] = useState("")

  const handleSearch = () => {
    // Implement search functionality here
    console.log("Searching with current state...")
  }

  return (
    <section className="p-4 space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <TagInput
          title="Technologies"
          placeholder="Enter Single or Multiple Technologies"
          tags={technologies}
          onAddTag={(tag) => setTechnologies([...technologies, tag])}
          onRemoveTag={(tag) => setTechnologies(technologies.filter((t) => t !== tag))}
          showMandatoryButton
        />
        <TagInput
          title="Companies"
          placeholder="Enter Single or Multiple Previous Companies"
          tags={companies}
          onAddTag={(tag) => setCompanies([...companies, tag])}
          onRemoveTag={(tag) => setCompanies(companies.filter((c) => c !== tag))}
        />
        <SingleInput title="Tech Role" placeholder="Enter Tech Role" value={techRole} onChange={setTechRole} />
        <SingleInput
          title="Current Location"
          placeholder="Enter Current Location"
          value={currentLocation}
          onChange={setCurrentLocation}
        />
        <TagInput
          title="Domain"
          placeholder="Enter Single or Multiple Domains"
          tags={domains}
          onAddTag={(tag) => setDomains([...domains, tag])}
          onRemoveTag={(tag) => setDomains(domains.filter((d) => d !== tag))}
        />
        <TagInput
          title="Preferred Location"
          placeholder="Enter Single or Multiple Locations"
          tags={preferredLocations}
          onAddTag={(tag) => setPreferredLocations([...preferredLocations, tag])}
          onRemoveTag={(tag) => setPreferredLocations(preferredLocations.filter((l) => l !== tag))}
        />
        <SingleInput
          title="Qualification"
          placeholder="Enter Qualification Details"
          value={qualification}
          onChange={setQualification}
        />
        <SingleInput
          title="Experience"
          placeholder="Enter Minimum Experience"
          value={experience}
          onChange={setExperience}
        />
        <SingleInput title="Salary" placeholder="Enter Minimum Salary" value={salary} onChange={setSalary} />
        <SingleInput
          title="Notice Period"
          placeholder="Enter Minimum Days"
          value={noticePeriod}
          onChange={setNoticePeriod}
        />
      </div>
      <div className="flex justify-end">
        <button onClick={handleSearch}>Search Candidates</button>
      </div>
    </section>
  )
}

